function Scrollers(params) {
    'use strict';
    var self = this;

    self.elems = {
        $scrolls: $('.hor-wrapper')
    };

    if (!self.elems.$scrolls.length) {
        return null;
    }

    self.settings = {
        wToggle: 768
    };
    $.extend(self.settings, params);
    self.cycleScroll = [];
    self.isMobDevice = (/android|webos|iphone|ipad|ipod|blackberry|iemobile|touch|opera mini/i.test(navigator.userAgent.toLowerCase()));
    self.init(params);

    return this;
}

Scrollers.prototype = {
    init: function () {
        'use strict';
        var self = this;

        // cycle slider
        self.resizeScrollers();
        self.controls();

        return this;
    },
    resizeScrollers: function () {
        'use strict';
        var self = this;

        self.elems.$scrolls.each(function (key, value) {
            var $scroller = $(value).find('.hor-scroller').eq(0),
                $items = $(value).find('.hor-sizing'),
                $snaps = $(value).find('.hor-snap'),
                $snapMob = $(value).find('.hor-snap-mobile'),
                width = 0,
                wToggle = self.settings.wToggle;

            // count width for scroller
            if ($items.length) {
                $items.each(function (key, value) {
                    width += $(value).width() + parseInt($(value).css('margin-left')) + parseInt($(value).css('margin-right')) + parseInt($(value).css('padding-left')) + parseInt($(value).css('padding-right'));
                });
                width += parseInt($items.eq(0).css('margin-left')) + parseInt($items.eq(0).css('padding-left'));
            }
            $scroller.css({
                width: width
            });

            // destroy old scrollers if needs
            if (self.cycleScroll[key]) {
                self.cycleScroll[key].destroy();
                self.cycleScroll[key] = null;
                self.cycleScroll.splice(key, 1);
            }

            setTimeout(function () {
                /*$(value).css({
                 height: $scroller.height()
                 });*/
                var dataInd = $(value).attr('data-indicators'),
                    $inds = dataInd ? $('#' + dataInd).find('.hor-ind') : [],
                    settings = {
                        scrollX: true,
                        scrollY: false,
                        useTransform: true,
                        interactiveScrollbars: true,
                        scrollbars: $scroller.hasClass('hor-scrollbar') ? 'custom' : false,
                        resizeScrollbars: false
                    };

                // parallax
                if ($inds.length) {
                    settings.indicators = [{
                        el: $inds.eq(0).parent()[0],
                        ignoreBoundaries: true,
                        speedRatioX: 0.2,
                        resize: false,
                        listenY: false
                    },{
                        el: $inds.eq(1).parent()[0],
                        ignoreBoundaries: true,
                        speedRatioX: 0.5,
                        resize: false,
                        listenY: false
                    }]
                }

                // mobiles
                if (self.isMobDevice) {
                    $.extend(settings, {
                        useTransform: true,
                        useTransition: false,
                        interactiveScrollbars: false,
                        eventPassthrough: true
                    });
                };

                // snap for mobiles
                if ($snapMob.length && self.isMobDevice) {
                    if ($(window).width() < wToggle && $snaps.length) {
                        $.extend(settings, {
                            snap: '.hor-snap'
                        });
                    }
                }

                var curScroll = {};
                self.cycleScroll.push(
                    curScroll = new IScroll(value, settings)
                );

                // prevent click
                curScroll.off('scrollStart');
                curScroll.off('scrollEnd');
                curScroll.on('scrollStart', function () {
                    $(value).addClass("dragging");
                });
                curScroll.on('scrollEnd', function () {
                    $(value).removeClass("dragging");
                });

            });

        });
        return this;
    },
    controls: function () {
        'use strict';
        var self = this,
            resizeTimeout;

        $(window).on({
            resize: function () {
                if (self.cycleScroll.length) {
                    clearTimeout(resizeTimeout);
                    resizeTimeout = setTimeout(function () {
                        self.resizeScrollers();
                    }, 200);
                }
            }
        });

        // prevent click if scrolling
        $(document).on({
            click: function (event) {
                event.preventDefault();
            }
        }, '.dragging');


        // hands cursors for scrollers
        $(document).on({
            mousedown: function (event) {
                $(this).addClass('catched');
            },
            mouseup: function (event) {
                $(this).removeClass('catched');
            }
        }, '.hor-wrapper');

        return this;
    }
};
$(document).ready(function () {
    window.scrollers = new Scrollers();
});
